<template>
  <div class="about">
    <p>email: {{email}}</p>
  </div>
</template>

<script>
import liff from '@line/liff'
export default {
  name: 'ABOUT',
  props: {
    msg: String
  },
  data () {
    return {
      email: ''
    }
  },
  created () {
    this.email = liff.getDecodedIDToken().email
    console.log(this.email)
  }
}
</script>

