</template>

<script>
import liff from '@line/liff'
import Header from './components/Header.vue'
export default {
  name: 'App',
  components: {
    Header
  },
  data () {
    return {
      profile: {},
      email: ''
    }
  },
  beforeCreate () {
    liff
      .init({
        liffId: '1657816678-d6o85QZa',
        withLoginOnExternalBrowser: true
      }).then(() => {
        this.getprofile()
        this.email = liff.getDecodedIDToken().email
        console.log(this.email)
      })
  },
  methods: {
    subscribe () {
      alert('恭喜你，訂閱成功')
    },
    confirm (num) {
      if (num === 0) {
        alert('錯了吧')
      } else {
        alert('不錯喔~')
      }
    },
    getprofile () {
      liff
        .getProfile()
        .then((profile) => {
          this.profile = profile
        })
    }
  }
}
</script>

